<template>
  <div class="generation-statistics">
    <SmallModule title="发电统计">
      <template #content>
        <div class="content-layout">
          <div>
            <NumberDisplay
              style="margin-bottom: 1.6vh"
              title="累计发电量(万kWh)"
              :value="genIndexData.dayGen"
              colorStyle="blue"
              @click="handleElecIndex"
            />
            <NumberDisplay
              title="累计上网电量(万kWh)"
              :value="genIndexData.dayOnline"
              colorStyle="green"
              @click="handleElecIndex"
            />
          </div>
        </div>
      </template>
    </SmallModule>
  </div>
</template>

<script lang="ts" setup>
  import SmallModule from '/@/components/Framework/OperationScreen/SmallModule.vue';
  import NumberDisplay from '/@/components/Framework/OperationScreen/NumberDisplay.vue';
  import { reactive, UnwrapRef, onMounted } from 'vue';
  import { getPowerStatData } from './data';
  import { assign } from 'lodash-es';
  import { addTabPage } from '/@/utils/route';

  //搜索参数
  const searchInfo: UnwrapRef<any> = reactive({});
  const genIndexData: UnwrapRef<any> = reactive({});

  function handleElecIndex() {
    addTabPage('/bi/elec/powerContrast', '电量对比统计', {
      stationId: props.overviewSearchInfo.stationUUId,
    });
  }

  interface Props {
    overviewSearchInfo?: {};
  }
  const props = withDefaults(defineProps<Props>(), {});
  onMounted(() => {
    assign(searchInfo, props.overviewSearchInfo);
    queryPowerStatData();
  });

  const queryPowerStatData = async () => {
    let data = await getPowerStatData(searchInfo);
    if (data.result) {
      assign(genIndexData, data.result);
    }
  };
</script>

<style lang="less" scoped>
  .content-layout {
    display: flex;
    align-items: center;
    height: 100%;
    padding-left: 0.16rem;
  }
</style>
